
import { computed, defineComponent } from 'vue'
import { searchFormConfig } from './config/search.config'
import PageSearch from '@/components/page-search/index'
import { useStore } from 'vuex'
import YlTable from '@/base-ui/table'

export default defineComponent({
  name: 'user',
  components: { PageSearch, YlTable },
  setup() {
    const store = useStore()
    store.dispatch('system/getPageListAction', {
      pageUrl: 'users/list',
      queryInfo: {
        offset: 0,
        size: 10
      }
    })
    const userList = computed(() => store.state.system.userList)

    const propList = [
      { prop: 'id', label: 'Id', minWidth: '180' },
      { prop: 'name', label: 'Name', minWidth: '180' },
      { prop: 'realname', label: '真实姓名', minWidth: '180' },
      { prop: 'roleId', label: '角色', minWidth: '180' },
      { prop: 'cellphone', label: '电话号码', minWidth: '180' },
      { prop: 'createAt', label: '创建时间', minWidth: '250' }
    ]
    const deleteRow = (index: number) => {
      userList.value.splice(index, 1)
    }

    const now = new Date()
    const onAddItem = () => {
      now.setDate(now.getDate() + 1)
      userList.value.push({
        id: userList.value.length + 1,
        name: prompt('请输入你的昵称'),
        realname: prompt('输入你的真实姓名'),
        roleId: 4,
        cellphone: prompt('输入你的手机号码'),
        createAt: now,
        enable: 1
      })
      userList.value.sort(function (a: any, b: any) {
        return a.id - b.id
      })
    }
    return {
      searchFormConfig,
      userList,
      propList,
      deleteRow,
      onAddItem
    }
  }
})
